@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
*{margin:0;padding:0;outline:0;box-sizing:border-box}body{font:400 14px Roboto, sans-serif;background-color:#f0f0f5;-webkit-font-smoothing:antialised}input,button,textarea{font:400 18px Roboto, sans-serif}

.home-container{display:flex;justify-content:space-between;align-items:center;width:100%;max-width:750px;margin:0 auto}.home-container .logo{width:300px}.home-container form{display:flex;align-items:center}.home-container form input{width:100%;height:50px;color:#333;border:1px solid #dcdce6;border-radius:8px;padding:0 24px;text-align:center;box-shadow:0 0 100px rgba(0,0,0,0.1)}.home-container form button{width:100%;height:30px;color:#333;padding:0 24px;border-radius:8px;outline:none;border:1px solid #333;margin-top:3px;box-shadow:0 0 100px rgba(0,0,0,0.1)}.home-container form span{margin-top:10px}.grid-container{display:grid;grid-template-columns:repeat(5, 1fr)}.grid-container .section-people{display:flex;align-items:center;width:300px;font-size:20px;flex-direction:column;border:1px solid black;margin:0 auto;margin-bottom:50px;padding:10px}.arrow{padding:20px;display:flex;justify-content:space-between}

.container-page{width:100%;display:flex;flex-direction:column;align-items:center;margin:20px auto}

